#card {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 50px 0 100px 0;
}

.box {
  margin: 2rem;
  width: 28%;
}


.box img {
  border-radius: 2rem;
  height: 48vh;
  width: 100%;
}

.box p {
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
  margin: 1.2rem;
}

@media only screen and (max-width:800px) {
  .box {
    margin: 1rem;
    width: 80%;
  }


  .box img {
    border-radius: 2rem;
    height: 48vh;
    width: 100%;
  }

  .box p {
    font-size: 1rem;
    font-weight: 600;
    text-align: center;
    margin: 1rem;
  }

}