/* signup */
.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    width: inherit;
}

.wrapper h2 {
    margin-bottom: 15px;
    color: white;
}

input {

    padding: 18px 30px;
    margin: 15px;
    outline: none;
    border: 1px solid #363131;
    border-radius: 10px;
    font-size: 20px;

}

.wrapper button {
    background-color: #3861fb;
    color: white;
    border: none;
    outline: none;
    width: 30%;
    border-radius: 10px;
    padding: 15px 30px;
    cursor: pointer;
    font-weight: bold;
    font-size: 20px;
    margin: 10px;
    margin-top: 15px;
}

